import React, { useState, useEffect } from 'react'
import { Card, CardBody, Heading, Text } from 'uikit'
import BigNumber from 'bignumber.js/bignumber'
import styled from 'styled-components'
import { getBalanceNumber } from 'utils/formatBalance'
import { useTotalSupply, useBurnedBalance } from 'hooks/useTokenBalance'
import useI18n from 'hooks/useI18n'
import { getVoodooAddress } from 'utils/addressHelpers'
import CardValue from './CardValue'
import { useFarms, usePriceVoodooBnb, useTotalValue } from '../../../state/hooks'
import TextCardValue from './TextCardValue'

const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`
const useFetch = (uri: string) => {
  const [data, setData] = useState(null)

  // empty array as second argument equivalent to componentDidMount
  useEffect(() => {
    async function fetchData() {
      const response = await fetch(uri)

      const json = await response.json()
      setData(json.result)
    }
    fetchData()
  }, [uri])

  return data
}

const Divider = styled.div`
  background-color: ${({ theme }) => theme.colors.borderColor};
  height: 1px;
  margin: 18px auto;
  width: 100%;
`

const necrodevbalance = (necroAddress, address = '0x000000000000000000000000000000000000dead') =>
  `https://api.bscscan.com/api?module=account&action=tokenbalance&contractaddress=${necroAddress}&address=${address}&tag=latest&apikey=VSHI3UDSF7XUCTQ5ZKY6SBQ985HACXNJUN`

const CakeStats = () => {
  const TranslateString = useI18n()
  const necroAddress = getVoodooAddress()
  const totalValue = useTotalValue()
  const totalSupply = useTotalSupply()
  const burnedBalance = useBurnedBalance(getVoodooAddress())
  const farms = useFarms()
  const necroPrice = usePriceVoodooBnb()
  const circSupply = totalSupply ? totalSupply.minus(burnedBalance) : new BigNumber(0)
  const cakeSupply = getBalanceNumber(circSupply)
  const marketCap = necroPrice.times(circSupply)

  const necrodevbalanceCount22 = useFetch(necrodevbalance(necroAddress))
  const Voodoodevbalbig22 = getBalanceNumber(new BigNumber(necrodevbalanceCount22)).toFixed(2)

  let tokenPerBLock = 0

  if (farms && farms[0] && farms[0].tokenPerBLock) {
    tokenPerBLock = new BigNumber(farms[0].tokenPerBLock).div(new BigNumber(10).pow(18)).toNumber()
  }

  return (
    <StyledCakeStats>
      <CardBody>
        <Heading size="lg" mb="24px">
          {TranslateString(999, 'Total Value Locked (TVL)')}
        </Heading>
        <CardValue value={getBalanceNumber(totalValue, 2)} prefix="$" decimals={2} />
        <Divider />
        <Row>
          <Text fontSize="14px">Total VOODOO Supply</Text>
          {cakeSupply && <CardValue fontSize="14px" value={cakeSupply} decimals={0} />}
        </Row>
        <Row>
          <Text fontSize="14px">Total VOODOO Burned</Text>
          <TextCardValue fontSize="14px" value={Voodoodevbalbig22} />
        </Row>
        <Row>
          <Text fontSize="14px"> VOODOO price </Text>
          <CardValue fontSize="14px" value={necroPrice.toNumber()} />
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(999, 'Market Cap')}</Text>
          <CardValue fontSize="14px" value={getBalanceNumber(marketCap)} decimals={0} prefix="$" />
        </Row>
        <Row>
          <Text fontSize="14px">{TranslateString(540, 'New VOODOO/block')}</Text>
          <TextCardValue fontSize="14px" value={tokenPerBLock.toString()} />
        </Row>
      </CardBody>
    </StyledCakeStats>
  )
}

export default CakeStats
