import React from 'react'
import { Text } from 'uikit'

interface TextCardValueProps {
  value: string
  fontSize?: string
  prefix?: string
}

const TextCardValue: React.FC<TextCardValueProps> = ({ value, fontSize = '40px' }) => {
  return (
    <Text color="textSubtle" bold fontSize={fontSize}>
      {value}
    </Text>
  )
}

export default TextCardValue
